* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
::-webkit-scrollbar {
  width: 6px;
  cursor: pointer;
  height: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f5f5f5;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #aaa;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
body {
  background: #21e6c1;
  padding: 20px;
}
.container {
  width: 100%;
  min-height: 100vh;
  font-family: "B612", sans-serif;
  display: flex;
  flex-direction: column;
  border-radius: 15px;
}
.header {
  background: #2c2f43;
  color: white;
  text-align: center;
  padding: 10px;
  margin: 0.25px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  display: flex;
  justify-content: space-between;
  font-size: 0.8em;
}
.wrapper {
  display: flex;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12);
}
.editor {
  width: 50%;
  height: 85vh;
  border-radius: 10px;
}
textarea {
  width: 100%;
  height: 85vh;
  outline: none;
  resize: none;
  overflow: scroll;
  padding: 23px;
  background: #002651;
  color: #f0f0f0;
  font-family: "Fira Code", monospace;
}
ul {
  list-style-type: circle;
}
#preview {
  width: 50%;
  padding: 20px;
  background: #fff4e3;
  color: black;
  height: 85vh;
  line-height: 1.9;
  overflow: auto;
}
table {
  border-collapse: collapse;
}
th,
td {
  border: 2px solid orange;
  padding-left: 5px;
  padding-right: 5px;
}
blockquote {
  border-left: 3px solid #224b4b;
  color: #224b4b;
  padding-left: 5px;
  margin-left: 25px;
}

code {
  background: #ffb174;
  color: #111;
  padding: 1px 4px 2px 4px;
  font-size: 12px;
  font-weight: bold;
}

pre {
  background: #ffb174;
  border-radius: 5px;
  color: black;
  padding: 5px 0 5px 5px;
}
@media only screen and (max-width: 600px) {
  .wrapper {
    flex-direction: column;
  }
  .editor {
    width: 100%;
    height: 80vh;
  }
  #preview {
    width: 100%;
  }
}
